<template>
  <section>
    <loader :is-visible="isLoading"></loader>
    <tg-list v-show="IsExpenseListView"
        :listType="['List', 'Card']"
        :IsShowAction="true"
        ModuleName="Expenses"
        SubModuleCode="Expenses"
        IdentityColumn="expense_id"
        :HeaderText="$t('Expense')"
        :ListData="ExpenseData"
        :HeaderData="Headers"
        :widgets="widgets"
        :HeaderButtons="listheaderbuttons"
        :TotalRecords="TotalRecords"      
        :SearchFields="leftSearchFields" 
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"   
        :KanbanDataCallBackFunction="FetchDataKanban"       
        :SortExp="SortExp"
        :SortBy="SortBy"
        :LegendArray="LegendArray"
       :NorecordfoundText="$t('NoRecordfound')"
        v-on:LeftsearchButtonClick="leftCommonSearch" 
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" 
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'status_id'">                
                <a class="inactive-disabled">
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')" v-if="data.row.status_id !='Active' && data.row.status_id !='ApprovalPending'">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')" v-if="data.row.status_id =='ApprovalPending'">
                        {{ $t('ApprovalPending') }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')" v-if="data.row.status_id=='Active'">
                        {{ $t('Draft') }}
                    </span>                     
                </a>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'expense_amount'">                                  
                    {{convertDecimalValueInFormat(data.row.expense_amount) +' '+data.row.Expense_amount_iso_code}}                   
            </template>
            <template v-if="data.column.COLUMN_NAME == 'CONVERSION_AMOUNT'"> 
                    {{convertDecimalValueInFormat(data.row.CONVERSION_AMOUNT) +' '+ data.row.convertion_amount_iso_code}}
                                   
            </template> 
        </template> 
        <template v-slot:customcard="{data}">
            <div class="card-box expense-card text-center pb-2" v-bind:class="getStatusClassCard(data.row.status_id)">
                <div class="expense-card-header"><div class="custom-control custom-checkbox">
                    <input type="checkbox" class="chkItems"  v-on:change="OnChange()" :value="data.row.expense_id" v-bind:disabled="data.row.isCheckBoxDisabled" id="chk_11" style="display: none;">
                    <label class="custom-control-label universal-custom-control-label pt-1" for="chk_11"></label>
                </div> 
                <span class="expense-icon"><i class="fa  fa-file-text-o"></i></span> 
                <a  title="" class="text-truncate expense-title"  :value="data.row.expense_id" data-toggle-tooltip="tooltip" style="cursor: default;" data-original-title="gfhghgfh">{{ data.row.expense_title }}</a>
            </div> 
            <div class="card-detail-main">
                <div class="expense-project-date">
                    <span>{{data.row.project_id}}</span><span>{{data.row.expense_date}}</span>{{ data.row.rownumber }}
                </div>
            </div> 
            <div class="expense-amount">
                <div class="Task">
                    <span class="t-title"> {{convertDecimalValueInFormat(data.row.expense_amount) +' '+data.row.Expense_amount_iso_code}}</span>
                </div>
            </div> 
            <a :id="'a'+data.row.rownumber" @click="ShowMore('showdiv'+data.row.rownumber)" href="javascript:;" :class="{'read-more-content':true,'hide':ShowMoreFalse==true}">{{$t('More')}}</a> 
            <div :class="{'read-more-content':true,'hide':ShowMoreFalse==false}" :id="'showdiv'+data.row.rownumber">
                <div class="expense-project-date border-top float-left">
                    <span><strong class="w-100 d-block">{{ $t('expense_category_id') }}</strong>{{ data.row.expense_category_id }}</span>
                    <span><strong class="w-100 d-block">{{$t('trip_id')}}</strong>{{ data.row.trip_id }}</span>
                </div> 
                <div class="text-center" :id="'showdiv'+data.row.rownumber">
                    <span class="border-0">
                        <strong class="w-100 d-block">{{$t('OrignalConversionAmount')}}</strong>{{convertDecimalValueInFormat(data.row.CONVERSION_AMOUNT) +' '+data.row.convertion_amount_iso_code}}</span>
                </div> 
                    <a id="cardMoreDataLess" @click="ShowLess('showdiv'+data.row.rownumber)" href="javascript:;" :class="{'read-more-hide':true,'':ShowMoreFalse==true}">{{ $t('Less') }}</a></div> <div class="verticle-action">
                        <em class="fa fa-ellipsis-v action_icon"  @click="openActionIcon($event)" aria-hidden="true"></em>
                        <span class="action-list-box" style="width: 240px;display:none;">
                            <span class="list-actions fsm-list" id="action-list" style="padding-left:0">
                                <a v-bind:class="{'disabled':data.row.status_id !='Active'}" class="update actions-onclick" :href="'/Expense/ManageExpense/'+ data.row.expense_id + '/Yes/Expense/Expense'"><em class="fa fa-pencil action-icon"></em><span class="text">{{$t('Edit')}}</span></a>
                                <a v-bind:class="{'disabled':data.row.filescount==0}"  href="javascript:;" class="update actions-onclick" @click="OpenFileModal(data.row)"><em class="fa fa-eye action-icon"></em><span class="text">{{$t('ViewAttachedFile')}}</span></a>
                                <a v-bind:class="{'disabled':data.row.commentscount==0}" href="javascript:void(0)" class="update actions-onclick" @click="openAddNewEvent(data.row)"><em class="fa fa-comment action-icon"></em><span class="text">{{ $t('Comments') }}</span></a>
                                <a href="javascript:;" class="actions-onclick" @click="SendForApproval(data.row)"><em class="fa fa-file text-warning action-icon"></em><span class="text">{{$t('SendForApprovalTitle')}}</span></a>
                                <em class="fa fa-times close close-action" @click="closeActionIcon($event)" aria-hidden="true"></em>
                            </span>                                   
                        </span>                                    
                    </div>                    
            </div>
        </template>
       </tg-list>
    <ExpenseAttachents v-if="showFileModal" @CloseExpenseAttachments='CloseExpenseAttachments' v-bind:expenseid="expid" v-bind:code="statuscode" ></ExpenseAttachents>
    <ExpenseComments v-if="showCommentModal" @CloseExpenseComments='CloseExpenseComments' :access="access" v-bind:expenseid="expid" :CommentHeader="CommentHeader" :isSingleCommentforMultipleExpenses="isSingleCommentforMultipleExpenses" :statuscode="statuscode" :visibility="visibility"></ExpenseComments>
  </section>  
</template>
<script>
    import ExpenseDataServices from "@/services/DataService";
    import Schema from "@/views/Expense/Expense/ExpenseSchema";
    import ExpenseAttachents from '../../../components/Common/Modal/ExpenseAttachments.vue';
    import ExpenseComments from '../../../components/Common/Modal/ExpenseComments.vue'
   export default { 
      components: {
        ExpenseAttachents,
        ExpenseComments
      },
    data(){
        return{
            isSingleCommentforMultipleExpenses:null,
            CommentHeader:'',
            isLoading:false,
            searchCondition: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            Headers:[],
            StatusList: [],
            ExpenseData:[],
            cardViewData:[],
            kanbanViewData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            noRecord:false,
            CurrentPage: 1,
            TotalPages: 0,
            IsExpenseListView: true,
            IsExpenseListViewHide: false,
            IsExpenseCardView: false,
            IsExpenseCardViewHide: true,
            IsExpenseKanbanView: false,
            IsExpenseKanbanViewHide: true,
            listActions: Schema.listActions,
            showFileModal:false,
            expid:'',
            statuscode:'',   
            ExpenseStatusList: null,
            conditionForInstantSearch: "",
            showCommentModal:false,
            showCommentModal:false,
            StatusListForListing:[],
            widgets: [
                {
                    icon: "fa fa-dollar",
                    value: "12",
                    name: "Total Expenses",
                    class:"one_wid"
                },
                {
                    icon: "fa fa-dollar",
                    value: "7",
                    name: "Approved Expenses",
                    class:"two_wid"
                },
                {
                    icon: "fa fa-clock-o",
                    value: "5",
                    name: "Approval Pending",
                    class:"three_wid"
                },
                {
                    icon: "fa fa-reply",
                    value: "1",
                    name: "Send for Correction",
                    class:"four_wid"
                }
            ],
            LegendArray:[{
                class:'',
                backgroundColor:'#17a2b8',
                name:'Draft'
            },
            {  class:'legend-Progress',
                backgroundColor:'',
                name:'Approved'
            },
            {
                class:'legend-pending',
                backgroundColor:'',
                name:'Pending'
            },
            {
                class:'',
                backgroundColor:'#fe6847',
                name:'SendForCorrection'
            },
            {
                class:'',
                backgroundColor:'#ee1313',
                name:'Rejected'
            }

        ],
            ShowMoreFalse:false,
            visibility:0,
            access:1
        }
    },
    created: function () {
        var vm=this;
        vm.usertype = vm.GetUserInfo.UserType;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.GetExpenseStatusList();
        vm.onStatusList();
        vm.FetchData();
        vm.getStatuslistForListing('Expense', 'en');
        Schema.bindleftCommonSearchdropdown(vm, "Expense", "EXPENSE_PROJECT");  
        Schema.bindleftCommonSearchdropdown(vm, "Expense", "EXPENSE_STATUS"); 
    },
    updated: function () {
        this.BindActionButtonEvent();
        this.SetScrolbar('dark-thick','', 'filterscroll_leftsearch');
    },
    methods:{
        openActionIcon(e){
            e.target.nextSibling.removeAttribute('style');
            e.target.nextSibling.setAttribute('style', 'width:240px;');
            e.target.nextSibling.style.display = 'block';
        },
        closeActionIcon(e){ 
            e.target.nextSibling.style.display = 'none';
        },
        getStatusClassList:function(status){            
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass='px-2 text-center text-white d-block bg-info';
                    break;
                case 'Approved':                 
                    returnClass='px-2 text-center text-white d-block bg-success';
                    break;
                case 'ApprovalPending':
                    returnClass='px-2 text-center text-white d-block bg-warning';
                    break;
                case 'Rejected':
                    returnClass='px-2 text-center text-white d-block bg-danger';
                    break;
                case 'SentForCorrection':
                    returnClass='px-2 text-center text-white d-block bg-orange';
                    break;  
                default:
                    returnClass = '';
                }  
                return returnClass;
        },
        getStatusClassCard: function (status) {  
            debugger;
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass='ex-blue-bg-light';
                    break;
                case 'Approved':                 
                    returnClass='ex-green-bg-light';
                    break;
                case 'Approval Pending':
                    returnClass='ex-yellow-bg-light';
                    break;
                case 'Rejected':
                    returnClass='ex-red-bg-light';
                    break;
                case 'SentForCorrection':
                    returnClass='ex-orange-bg-light';
                    break;
                case 'Draft':
                    returnClass='ex-blue-bg-light';
                    break; 
                default:
                    returnClass = '';
                }  
                return returnClass;
        },
        DisableInputBox :function(status){
            var returnClass = '';
            switch (status) {
                case 'Approved': 
                case 'Active':                    
                case 'ApprovalPending':                    
                case 'Rejected':
                    returnClass='disbaled';
                    break;
                case 'SentForCorrection':                    
                case 'Draft':
                    returnClass='disbaled';
                    break; 
                default:
                    returnClass = '';
                }  
                return returnClass;
        },
        updateURLParameter(url, param, paramVal) {
            var newAdditionalURL = "";
            var tempArray = url.split("?");
            var baseURL = tempArray[0];
            var additionalURL = tempArray[1];
            var temp = "";
            if (additionalURL) {
                tempArray = additionalURL.split("&");
                for (var i = 0; i < tempArray.length; i++) {
                    if (tempArray[i].split('=')[0] != param) {
                        newAdditionalURL += temp + tempArray[i];
                        temp = "&";
                    }
                }
            }
            var rows_txt = temp + "" + param + "=" + paramVal;
            return baseURL + "?" + newAdditionalURL + rows_txt;
        },
        ShowMore(ths){        
             $('#'+ths).show();
             $('#a'+ths.split('v')[1]).hide();
        },
        ShowLess(ths){       
             $('#'+ths).hide();
             $('#a'+ths.split('v')[1]).show();
        },
        FetchData:function(){                         
            var vueObj = this;
            if (vueObj.IsExpenseKanbanView) {
                vueObj.FetchDataKanban();
                    return false;
                }
            vueObj.isLoading = true;   
            const params = this.getRequestParams(this.sortBy,this.sortExp,this.pageSize,this.pageNumber,this.isPaged, this.searchcondition);          
            params.pageSize = vueObj.PageSize;
            params.pageNumber = vueObj.PageNumber;
            params.searchCondition = vueObj.searchCondition;
            if (vueObj.PageNumber) { params.pageNumber = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNumber = 1; }
            if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            else { vueObj.isPaged = params.isPaged = 1; }
            params.isPaged = vueObj.isPaged;
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            var srchCondition = "";
            if (!vueObj.isBlank(vueObj.searchCondition)) { srchCondition = srchCondition + vueObj.searchCondition; }
            if (!vueObj.isBlank(vueObj.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!vueObj.isBlank(vueObj.searchCondition)) ? (" AND " + vueObj.conditionForInstantSearch) : vueObj.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchCondition = srchCondition; 
            }
            ExpenseDataServices.ExpenseListing(params).then(function (response){  
                vueObj.isLoading = false;
                if(response.data != null) 
                {                 
                    vueObj.Headers=response.data.schema;   
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;                                
                        if(column.COLUMN_NAME == 'status_id'){
                            column.DISPLAY_NAME='Status'
                            column.settings = {
                                isInSlot : true,
                                isForTag: true
                            }
                        }
                        if(column.COLUMN_NAME == 'expense_amount'){
                            column.DISPLAY_NAME='Amount'
                            column.settings = {
                                isInSlot : true                               
                            }
                        } 
                        if(column.COLUMN_NAME == 'expense_category_id'){
                            column.DISPLAY_NAME='Expense Category'
                           
                        }                             
                        if(column.COLUMN_NAME == 'CONVERSION_AMOUNT'){
                            column.DATA_TYPE = "string";
                            column.settings = {
                                isInSlot : true                               
                            }
                        }                        
                    });                
                    if(response.data.data.length>0)
                    {          
                        vueObj.ExpenseData = [];
                        vueObj.TotalRecords=0;
                        vueObj.ExpenseData = response.data.data;
                        vueObj.ExpenseData.forEach((row)=>{
                       
                                row.isCheckBoxDisabled=false;
                                      if(row.status_code!='ACT' && row.status_code!='EXP_SEND_FOR_CORRECTION'){
                               
                                        row.isCheckBoxDisabled=true;
                                         } 
                            
                            });
                        vueObj.cardViewData = vueObj.ExpenseData;
                        vueObj.TotalRecords = response.data.data[0].total_records;
                            if (response.data.total_expenses_widget.length == 0) {
                                vueObj.TotalExpenseWidget = 0;                                
                            }
                            else {
                                vueObj.widgets[0].value = vueObj.convertDecimalValueInFormat(response.data.total_expenses_widget[0]);
                            }
                            if (response.data.approved_expense_widget.length == 0) {
                                vueObj.ApprovedExpenseWidget = 0;
                                
                            }
                            else {
                                vueObj.widgets[1].value = vueObj.convertDecimalValueInFormat(response.data.approved_expense_widget[0]);
                            }
                            if (response.data.approval_pending_widget.length == 0) {
                                vueObj.PendingExpenseWidget = 0;
                            }
                            else {
                                vueObj.widgets[2].value = response.data.approval_pending_widget[0];
                            }
                            if (response.data.send_for_correction_widget.length == 0) {
                                vueObj.SendForCorrectionWidget = 0;
                            }
                            else {
                                vueObj.widgets[3].value = response.data.send_for_correction_widget[0];
                            }
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteItem']").removeClass('enable').addClass('disabled');
                            $("[id^='aCommanApproval']").removeClass('enable').addClass('disabled');
                            $(".close-action_cv").parent().parent(".action-list-box").animate({
                                width: "0px",
                            }, 200);
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.ExpenseData = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ExpenseData = [];
                        vueObj.Headers = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () {vueObj.CheckBoxBootstrap()}, 100);                    
                    setTimeout(function () {vueObj.ResponsiveDataTable('tablelistingdata')}, 500);
                }) 
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";                 
            ExpenseDataServices.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        getRequestParams: function (sortBy,sortExp,pageSize,pageNumber,isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        renderActions: function (row) { 
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {                
                let action = { ...actionItem };
                switch (action.title){                    
                    case 'Edit Expense':
                        action.isVisible= row.status_code=="ACT" || row.status_code=="EXP_SEND_FOR_CORRECTION";
                        break;
                    case 'View Files':
                        action.isVisible=row.filescount != 0;  
                        break;                      
                    case 'Comments': 
                        action.isVisible= row.commentscount != 0 || (self.usertype == 'CA' || self.usertype == 'PM' || self.usertype == 'NU') && row.status_code != 'ACT';
                        break;
                        case 'Send For Approval':
                        action.isVisible= row.status_code=='ACT' || row.status_code=='EXP_SEND_FOR_CORRECTION';
                        break;
                    default: {
                        action.isVisible = true;                        
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {        
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            debugger;
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {  
            switch (callbackfunction) {
                case 'Edit': 
                    break;                  
                case 'OpenFileModal': 
                    this.OpenFileModal(item);
                    break;                 
                case 'openAddNewEvent':
                    this.openAddNewEvent(item); 
                    break;                           
                case 'SendForApproval':                      
                     this.SendForApproval(item);                
                    break;   
            }
        },
        OpenFileModal:function(item){                             
            this.showFileModal=true;            
            this.expid = item.expense_id;
            this.statuscode = item.status_code;
        },
        CloseExpenseAttachments:function(){
            this.showFileModal=false;
        },
        sortdata: function (s) {            
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {            
            switch (event.callbackfunction) {  
                case "openaddExpense":
                    this.openaddExpense();
                    break;              
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break;
                case "ExpensesSendForApproval":
                    this.CommonApproval();
                    break;                
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;    
            }
        },
        openaddExpense:function(){            
            this.$router.push({name: "Manage Expense",path:'/Expense/ManageExpense',params:{id:"0",isEdit:"No",moduleName:"Expense",subModuleCode:"Expense"}})
        },
        OnChange: function () {
            debugger;
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#SendForApproval').removeClass('disabled');
                    $('#DeleteMultiple').removeClass('disabled');
                }
                else {
                    $('#SendForApproval').addClass('disabled');
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
    
        FetchDataKanban: function (type) {  
            debugger;              
                var vm = this;
                var url = "pagesize=" + this.PageSize + "&pagenumber=" + this.PageNumber + "&sortBy=" + this.SortBy + "&sortExp=" + this.SortExp;
                var srchCondition = "";
                if (!this.isBlank(this.searchCondition)) {
                    url = url + "&searchCondition=" + this.searchCondition
                }
                if (!this.isBlank(this.conditionForInstantSearch)) {
                    srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
                }
                if (srchCondition != "") {
                    url = url + "&searchCondition=" + srchCondition;
                }
                ExpenseDataServices.ExpenseKanbanListing(url).then(function (response) {
                    if (response.data != null) {
                        response.data.schema.forEach(header=>{
                           if(header.COLUMN_NAME=='status_id'){
                             header.DISPLAY_NAME='Status'
                            }
                        })
                        if (typeof response.data.data != "undefined") {
                            vm.TotalRecords = response.data.total_records[0];
                           
                            vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                            vm.CurrentPage = vm.PageNumber;
                            vm.noRecord = false;
                            if (response.data.approved_expense.length == 0) {
                                vm.ApprovedExpense = 0;
                            }
                            else {
                                vm.ApprovedExpense = response.data.approved_expense[0];
                            }
                            if (response.data.pending_expense.length == 0) {
                                vm.PendingExpense = 0;
                            }
                            else {
                                vm.PendingExpense = response.data.pending_expense[0];
                            }
                            if (response.data.active_expense.length == 0) {
                                vm.ActiveExpense = 0;
                            }
                            else {
                                vm.ActiveExpense = response.data.active_expense[0];
                            }
                            if (response.data.send_for_correction.length == 0) {
                                vm.SendForCorrectionExpense = 0;
                            }
                            else {
                                vm.SendForCorrectionExpense = response.data.send_for_correction[0];
                            }
                            if (response.data.rejected_expense.length == 0) {
                                vm.RejectedExpense = 0;
                            }
                            else {
                                vm.RejectedExpense = response.data.rejected_expense[0];
                            }
                            if (response.data.total_expenses_widget.length == 0) {
                                vm.TotalExpenseWidget = 0;
                            }
                            else {
                                vm.TotalExpenseWidget = response.data.total_expenses_widget[0];
                            }
                            if (response.data.approved_expense_widget.length == 0) {
                                vm.ApprovedExpenseWidget = 0;
                            }
                            else {
                                vm.ApprovedExpenseWidget = response.data.approved_expense_widget[0];
                            }
                            if (response.data.approval_pending_widget.length == 0) {
                                vm.PendingExpenseWidget = 0;
                            }
                            else {
                                vm.PendingExpenseWidget = response.data.approval_pending_widget[0];
                            }
                            if (response.data.send_for_correction_widget.length == 0) {
                                vm.SendForCorrectionWidget = 0;
                            }
                            else {
                                vm.SendForCorrectionWidget = response.data.send_for_correction_widget[0];
                            }
                        }
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        $("[id^='DeleteItem']").removeClass('enable').addClass('disabled');
                        $("[id^='aCommanApproval']").removeClass('enable').addClass('disabled');
                    }
                    else {
                        vm.ApprovedExpense = 0;
                        vm.PendingExpense = 0;
                        vm.SendForCorrectionExpense = 0;
                        vm.RejectedExpense = 0;
                    }
                    vm.MaxCount = Math.max(vm.ApprovedExpense, vm.PendingExpense, vm.SendForCorrectionExpense, vm.RejectedExpense);
                    // setTimeout(function () {
                    //     ScrollKanbanView();
                    // }, 500);
                    if (response.data != null && response.data.data.length > 0) {
                        vm.Headers = response.data.schema;
                        if (typeof response.data.data != "undefined") {
                            vm.ExpenseData = response.data.data;
                            if (parseInt($("#hdfStartIndex").val(), 10) > 1) {
                                vm.kanbanViewData.push.apply(vm.kanbanViewData, vm.ExpenseData)
                            }
                            else {
                                vm.kanbanViewData = vm.ExpenseData;
                            }
                            setTimeout("CheckBoxBootstrap()", 100);
                            // setTimeout("SliderDivListingScrool()", 100);
                            setTimeout("ResponsiveDataTable('tablelistingdata')", 500);
                            vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                            vm.CurrentPage = vm.PageNumber;
                            vm.noRecord = false;
                            
                        }
                        else {
                            vm.ExpenseData = [];
                            vm.noRecord = true;
                            vm.kanbanViewData = [];
                            vm.TotalRecords = 0
                           
                        }
                    }
                    else {
                        if (vm.TotalRecords <= 0) {
                            vm.ExpenseData = [];
                        }
                        vm.kanbanViewData = [];                      
                        
                        vm.Headers = response.data.schema;
                       
                    }
                });
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {                       
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Expenses")), function () {
                 var postJSON = {
                        ids: val,
                        moduleName: "Expenses",
                        subModuleCode: "EM_EXPENSE"
                    };                    
                    var objectPostString = JSON.stringify(postJSON);
                    ExpenseDataServices.CommonDeleteData(objectPostString).then(function (response) {
                        if (response.data[0].Status == 'Success') {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteItem']").removeClass('enable').addClass('disabled');
                            $("[id^='aCommanApproval']").removeClass('enable').addClass('disabled');                                    
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Expense')]), "Success", true, vm.$t('Alert'));
                            vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                if (vm.PageNumber < 0) {
                                    vm.PageNumber = 1;
                                }
                                vm.FetchData();                                    
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Expense')]), "failure", true, vm.$t('Alert'));                                   
                        } 
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },
        CommonApproval:function(event){  
            debugger;             
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                 vm.confirmR(vm.$t("ConfirmApproval"), true, 'Confirm', function () {
                    var objectPostString=`ExpenseId=${val}`;
                    ExpenseDataServices.SendForApproval(objectPostString).then(function (response) {
                        if(response.status==200)
                        {
                            
                            vm.FetchData();
                        }  
                        else{
                            vm.ShowAlert(vm.$t("SelectAnyExpenseToApproval"), "warning", true, vm.$t("Alert"));
                           
                        }                      
                        vm.isLoading=false;
                    });
                 });
            }

        },
        SendForApproval:function(event){
            debugger;
            var vm = this; 
            vm.confirmR(vm.$t("ConfirmApproval"), true, 'Confirm', function () {
               var objectPostString = `ExpenseId=${event.expense_id}`;
               ExpenseDataServices.SendForApproval(objectPostString).then(function (response) { 
                   if(response.status==200)
                   {
                       vm.FetchData();
                   }                       
                   
               });
            });
        },
        openAddNewEvent:function(item){ 
            debugger;
            this.CommentHeader='Comment'; 
            this.isSingleCommentforMultipleExpenses=0;          
            this.showCommentModal=true;
            this.expid = String(item.expense_id);
            this.statuscode = item.status_id;
            this.visibility=1;
            this.access=0;

        },
        CloseExpenseComments:function(){
           this.showCommentModal=false; 
        },
        GetExpenseStatusList: function () {
            var vm = this;
            var url = "moduleName=Expense&type=EXPENSE_STATUS";               
            ExpenseDataServices.getDDLData(url).then(function (response) {
                var json = response.data;                   
                if (json != null) {
                    vm.leftSearchFields[1].listOptions = json.DATA;
                    vm.ExpenseStatusList = json.DATA;
                }
            });
        },
        onStatusList: function () {
            var vm = this;
            var url = `langCode=en&companyId=${this.companyId}&statusType=Expense&hasglobal=true`;
            ExpenseDataServices.GetStatusDDLData(url)
                .then(function (response) {
                    if (response.data.status == "Success") {
                        var json = response.data.result;
                        vm.StatusList = json.data;
                        vm.activeData = $.grep(vm.StatusList, function (element, index) {
                            return element.STATUS_CODE == 'ACT';
                        });
                        vm.ApprovedData = $.grep(vm.StatusList, function (element, index) {
                            return element.STATUS_CODE == 'EXP_APPROVED';
                        });
                    }
            });
        },
    }
}
</script>
