<script>
import ExpenseDataService from "@/services/DataService";
export default {
    data(){
        return{
            isLoading:false,
            txtTripName:'',
            DDLList: [],
            STATUS_ID: 1001,
            txtTripDescription:''
        }
    },
    created: function () {        
        this.GetStatusList("Views", "en"); 
    },
    methods:{
        CloseExpenseTrip:function(){
            this.$emit('CloseExpenseTrip');
        },
        GetStatusList: function (StatusType, lang) {            
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            ExpenseDataService.GetStatusList(url).then(function (response) {                
                var json = response.data;
                vm.DDLList = json;
                console.log(vm.DDLList);
            });
        },
        SaveUpdateTrip:function(valid,errors) {
            var vm = this;
            var jsonobj = {
                TripId:0,
                TripName: vm.txtTripName,
                StatusId: vm.STATUS_ID,
                TripDesc:vm.txtTripDescription
            };
         
            var jsonobj = JSON.stringify(jsonobj);
            vm.isLoading=true;
            this.$refs.form.validate().then((result) =>{
                if (result) {
                    ExpenseDataService.SaveUpdateTrip(jsonobj).then((response)=>{      
                      debugger;                    
                         if(response.status == 200)
                         {
                            vm.$emit('CloseExpenseTrip');                                
                            vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('Trip')]), "success", true,  vm.$t("Alert"));
                         }
                         vm.isLoading=false;
                    });
                }
            });
        }
    }
}
</script>
<template>
  <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-header theme-primary partition-full">
                <h4 class="modal-title">{{$t('AddTrip')}}</h4>
                <button type="button" class="close" v-on:click="CloseExpenseTrip()">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
  
              <div class="modal-body">
                <Form ref="form" v-slot="{valid,errors}">
        <div class="col-md-12 p-0">            
                <div class="row">
                    <div class="col-lg-6">
                        <div class="form-group">                          
                                <label>Trip Name:<span class="text-danger">*</span></label>                                
                                  <Field name="TripName" :rules="{'required':true}" label="Trip Name" v-slot="{errors}">
                                    <input  v-bind:class="{ 'form-control': true, 'is-invalid':errors[0] }" v-model="txtTripName" type="text"  maxlength="200"   />                                           
                                    <span v-if="errors[0]" name="TripName" class="invalid-feedback">{{errors[0]}}</span>                                
                                 </Field>     
                        </div>
                    </div>
                    <div class="col-lg-6">
                        <div class="form-group">
                            <label>Status:<span class="text-danger">*</span></label>
                            <Field :rules="{'required':false}" name="status" v-model="STATUS_ID">
                               <select class="form-control valid" >
                                    <option v-for="(item,index) in DDLList" v-bind:value="item.STATUS_ID"  :key="index">
                                        {{item.STATUS}}
                                    </option>
                                </select>
                            </Field>
                            
                        </div>
                    </div>
                    <div class="col-lg-12">
                        <div class="form-group">
                            <label>Trip Description:</label>
                            <Field :rules="{'required':false}" name="txtTripDescription">
                                <textarea   name="txtTripDescription" class="form-control"
                                v-model="txtTripDescription" style="resize:none" cols="10" rows="5">
                                </textarea>
                            </Field>
                            
                        </div>
                    </div>
                </div>           
        </div>
        <div class="col-md-12 p-0">
                  <div class="row flex-row-reverse">
                    <div class="col-lg-6 text-right">
                      <a
                        href="javascript:;"
                        v-on:click="SaveUpdateTrip(valid,errors);"
                        title="Save"
                        class="btn btn-success save-btn mr-1"
                      >
                        <em class="fa fa-save pr-2"></em>
                        {{$t('Save')}}
                      </a>
                      <a
                        href="javascript:;"
                        v-on:click="CloseExpenseTrip()"
                        class="btn btn-danger  delete-btn"
                      >
                        <em class="fa fa-times"></em>
                        {{$t('Cancel')}}
                      </a>
                    </div>
                    <div class="col-lg-6">
                        <div class="py-2"><small class="text-danger">{{$t('MandatoryString')}}</small></div>
                    </div>
                  </div>
                </div>
        </Form>
              </div>              
            </div>
          </div>
        </div>
      </div>
    </transition>
  </template>